import React from 'react'
// import axios from 'axios';
import './Switch.css';

function Switch(props) {

    var handleChange = () => {
        let chk = !props.value;
        //sending via socket
        props.handleSocketSend({ "id": [props.valueId], "value": (+chk) });
    }

    return (
        <div className="col-6 col-sm-6  col-lg-4 col-xl-3">
            <div className="Switch">
                <label className="switch">
                    {(props.value) ?
                        <input type="checkbox" onChange={handleChange} checked={true} />
                        :
                        <input type="checkbox" onChange={handleChange} checked={false} />
                    }
                    <span className="slider"></span>
                </label>
            </div>
        </div>

    )
}

export default Switch
