import React, { useState } from 'react';
import { Link, withRouter } from 'react-router-dom'
import './Register.css';
import axios from 'axios';
import { authenticate } from '../helper/authHelper';
import Navbar from '../layouts/Navbar';
import Footer from '../layouts/Footer';
import {authToken} from '../helper/authHelper';

function AddNewDevice(props) {

    const [formData, setFormData] = useState({
        devicemodel: '',
        devicelabel: '',
        switchcount: ''
    });

    const [errorData, setErrorData] = useState([]);
    const [successAlert, setSuccessAlert] = useState('');
    const { devicemodel, devicelabel, switchcount } = formData;

    //Handling Input Change
    const handleInput = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    }

    //Handling Form Submit
    const handleSubmit = async (e) => {

        e.preventDefault();

        console.log(formData);
        try {

            const config = {
                headers: {
                    "Content-Type": "application/json",
                    "x-auth-token": authToken()
                }
            }

            const res = await axios.post('/api/switch-board/create', JSON.stringify(formData), config);
            setErrorData([]);
            setSuccessAlert(<div className="alert alert-success" role="alert">User Created Sucessfully</div>);
            props.history.push('/dashboard');
            setTimeout(() => { setSuccessAlert('') }, 4000);

        } catch (err) {
            var el = err.response.data.error;
            var errorMsg = [];
            if (err.response.data.error) {

                for (let i = 0; i < el.length; i++) {
                    errorMsg.push(<li key={"list" + i}> {el[i].msg} </li>);
                }
            }
            setSuccessAlert('');
            setErrorData(errorMsg);
        }
    }


    return (
        <>
            <Navbar />
            <div className="container pt-2">
                <div className="card bg-light">
                    <div className="card-body mx-auto" style={{ maxWidth: '400px' }} >
                        <h4 className="card-title mt-3 text-center text-uppercase">Add New Device</h4>

                        <code>
                            <ul>{errorData}</ul>
                        </code>
                        <form onSubmit={e => handleSubmit(e)}>
                            <div className="form-group">
                                <label className="text-muted">Device Model</label>
                                <input name="devicemodel" className="form-control" onChange={e => handleInput(e)} value={devicemodel} placeholder="e.g. L3F2D1" type="text" />
                                &nbsp;<small className="text-info">Model name will be available with device packet.<br />
                                    &nbsp;Example: R12x, L3F2D1
                                </small>
                            </div>

                            <div className="form-group">
                                <label className="text-muted">Installed Location</label>
                                <input name="devicelabel" className="form-control" onChange={e => handleInput(e)} value={devicelabel} placeholder="e.g. My Bed Room" type="text" />
                                &nbsp;<small className="text-info">The name where device is installed.<br />
                                    &nbsp;Example: Main Room, Bath Room, Drawing Room, <br />
                                    &nbsp;Kitchen
                                </small>
                            </div>

                            <div className="form-group">
                                <label className="text-muted">Number Of Switches </label>
                                <input name="switchcount" className="form-control" onChange={e => handleInput(e)} value={switchcount} placeholder="e.g. 3" type="number" />
                                &nbsp;<small className="text-info">The number of switches  present on device<br />
                                    &nbsp;Example: 2,3,4,6 or 8 switches.
                                </small>
                            </div>


                            <div className="form-group">
                                <button type="submit" className="btn btn-primary btn-block"> Add Device  </button>
                                <button type="button" className="btn btn-danger btn-block"> Cancel  </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            <Footer />
        </>
    )
}

export default withRouter(AddNewDevice);