import React, { useState } from 'react';
import { Link, withRouter } from 'react-router-dom'
import './Register.css';
import axios from 'axios';
import { authenticate } from '../helper/authHelper';
import Navbar from '../layouts/Navbar';
import Footer from '../layouts/Footer';

function Register(props) {

    const [formData, setFormData] = useState({
        firstname: '',
        lastname: '',
        email: '',
        phone_code: 91,
        phone: '',
        password: '',
        password_confrmation: ''
    });

    const [errorData, setErrorData] = useState([]);
    const [successAlert, setSuccessAlert] = useState('');
    const { firstname, lastname, email, phone_code, phone, password, password_confrmation } = formData;

    //Handling Input Change
    const handleInput = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    }

    //Handling Form Submit
    const handleSubmit = async (e) => {

        e.preventDefault();
        try {


            const config = {
                headers: {
                    "Content-Type": "application/json"
                }
            }


            const res = await axios.post('/api/user/register', JSON.stringify(formData), config);
            setErrorData([]);
            setSuccessAlert(<div className="alert alert-success" role="alert">User Created Sucessfully</div>);
            authenticate(res, () => {
                props.history.push('/dashboard');
            });
            setTimeout(() => { setSuccessAlert('') }, 4000);
            console.log(res.data);

        } catch (err) {
            var el = err.response.data.error;
            var errorMsg = [];
            if (err.response.data.error) {

                for (let i = 0; i < el.length; i++) {
                    errorMsg.push(<li key={"list" + i}> {el[i].msg} </li>);
                }
            }
            setSuccessAlert('');
            setErrorData(errorMsg);
        }
    }


    return (
        <>
            <Navbar />
            <div className="container pt-2">
                <div className="card bg-light">
                    <div className="card-body mx-auto" style={{ maxWidth: '400px' }} >
                        <h4 className="card-title mt-3 text-center">Create Account</h4>
                        <p className="text-center">Get started with your free account</p>
                        <p>
                            <Link to="/" className="btn btn-block btn-twitter"> <i className="fab fa-twitter"></i>   Login via Twitter</Link>
                            <Link to="/" className="btn btn-block btn-facebook"> <i className="fab fa-facebook-f"></i>   Login via facebook</Link>
                        </p>
                        <p className="divider-text">
                            <span className="bg-light">OR</span>
                        </p>
                        {successAlert}
                        <code>
                            <ul>{errorData}</ul>
                        </code>
                        <form onSubmit={e => handleSubmit(e)}>
                            <div className="form-group input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text"> <i className="fa fa-user"></i> </span>
                                </div>
                                <input name="firstname" className="form-control" onChange={e => handleInput(e)} value={firstname} placeholder="First Name" type="text" />
                            </div>
                            <div className="form-group input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text"> <i className="fa fa-user"></i> </span>
                                </div>
                                <input name="lastname" className="form-control" onChange={e => handleInput(e)} value={lastname} placeholder="Last Name" type="text" />
                            </div>
                            <div className="form-group input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text"> <i className="fa fa-envelope"></i> </span>
                                </div>
                                <input name="email" className="form-control" onChange={e => handleInput(e)} value={email} placeholder="Email address" type="email" autoComplete="off" />
                            </div>
                            <div className="form-group input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text"> <i className="fa fa-phone"></i> </span>
                                </div>
                                <select className="custom-select" name="phone_code" onChange={e => handleInput(e)} value={phone_code}>
                                    <option value="91">+91</option>
                                    <option value="81">+81</option>
                                </select>
                                <input name="phone" className="form-control" onChange={e => handleInput(e)} value={phone} placeholder="Phone number" type="text" />
                            </div>
                            <div className="form-group input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text"> <i className="fa fa-lock"> </i> </span>
                                </div>
                                <input name="password" className="form-control" onChange={e => handleInput(e)} value={password} placeholder="Create password" type="password" autoComplete="off" />
                            </div>
                            <div className="form-group input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text"> <i className="fa fa-lock"></i> </span>
                                </div>
                                <input name="password_confrmation" className="form-control" onChange={e => handleInput(e)} value={password_confrmation} placeholder="Repeat password" type="password" />
                            </div>
                            <div className="form-group">
                                <button type="submit" className="btn btn-primary btn-block"> Create Account  </button>
                            </div>
                            <p className="text-center">Have an account? <Link to="/login">Log In</Link> </p>
                        </form>
                    </div>
                </div>
            </div>
            <Footer />  
        </>
    )
}

export default withRouter(Register);