import React, { useEffect, useState } from 'react';
import SwitchBoard from './SwitchBoard';
import axios from 'axios';
import {authToken} from '../helper/authHelper';

function DashboardList() {

    const [switcBoard, setSwitchBoard] = useState([]);

    useEffect(() => {
        async function getData(){

            try {
                const config = {
                    headers: {
                        "Content-Type": "application/json",
                        "x-auth-token": authToken()
                    }
                }
                const getSwitchBoards = await axios.get('/api/switch-board/get', config);
                setSwitchBoard(getSwitchBoards.data);
            } catch (err) {
                console.log(err.message);
            }
        }
        getData();
    },[]);

    console.log(switcBoard);
    let boards = switcBoard.map(board=><SwitchBoard valueId={board.id} key={board.id} backColor={board.color} token={board.token} labelname={board.label} switches={[...board.switches]} />)

    return (
        <div className="row">
            {boards}
        </div>
    )
}

export default DashboardList
