import React from 'react';
import Navbar from '../layouts/Navbar';
import Footer from '../layouts/Footer';

function NotFoundPage() {
    return (
        <>
            <Navbar />
            <div className="row pl-5 pt-5 justify-text-center">
                <div>
                    <h2 style={{ fontSize: "200px", fontFamily: "fantasy" }}>404</h2>
                </div>
                <div>
                    <h1 style={{ fontSize: "100px" }}>Page Not Found </h1>
                </div>
            </div>
            <Footer />
        </>
    )
}

export default NotFoundPage
