import React, { useEffect, useState } from 'react';
import OnOff  from "react-detect-offline";
import Switch from './Switch';
import './SwitchBoard.css';


function SwitchBoard(props) {

    const [switches, setSwitches] = useState([...props.switches]);
    const [WS, setWs] = useState({});

    function connect() {
        var interval = null;
        const ws = new WebSocket(`ws://13.48.3.212?from=client_${props.valueId}&token=${props.token}`);
        // const ws = new WebSocket(`ws://192.168.1.103:5000?from=client_${props.valueId}&token=${props.token}`);
        ws.onopen = () => {
            clearTimeout(interval);
            // on connecting, do nothing but log it to the console
            console.log(`client_${props.valueId} connected`);
        }

        ws.onclose = () => {
            console.log(`client_${props.valueId}  disconnected`);
            // automatically try to reconnect on connection loss
            console.log('Cannot reach server trying to reconnect');
            interval = setTimeout(() => {
                let webSocket = connect();
                setWs(webSocket);
            }, 3000);

        }

        return ws;
    }

    WS.onmessage = evt => {

        var reader = null;
        if (evt.data instanceof Blob) {
            
            reader = new FileReader();
            reader.onload = () => {
                handleMessage(reader.result);
            };
    
            reader.readAsText(evt.data);
        } else {
            handleMessage(evt.data);
        }
        
    }

    function handleMessage(msg) {
        // listen to data sent from the websocket server

        try {
            const message = JSON.parse(msg);

            console.log(msg);

            let switch_t = switches.map(d => {
                if (message.id == d.id) {
                    return { ...d, value: message.value };
                }
                return d;
            });

            setSwitches([...switch_t]);
        } catch (err) {
            console.log(msg);
        }
    }

    useEffect(() => {

        let webSocket = connect();
        setWs(webSocket);
        console.log("SwitchBoard useEffect first load");

    }, []);

    useEffect(() => {
        console.log("SwitchBoard useEffect switches");
    }, [switches]);




    function socketSend(switch_data) {

        let switch_t = switches.map(d => {
            if (switch_data.id == d.id) {
                return { ...d, value: switch_data.value }
            }
            return d;
        });


        setSwitches(switch_t);

        let switch_state = switch_t.map((d, i) => {
            return { "id": d.id, "value": d.value, "switch_id": i, "user_id": d.user_id };
        });
        console.log('------connection debug-------');
        console.log(WS.readyState);
        console.log('-----------------------------');

        if (WS.readyState !== 1) {
            setTimeout(() => {
                let webSocket = connect();
                setWs(webSocket);
            }, 2000);
            alert('Connection lost please re-try');
            WS.send(JSON.stringify(switch_state));
        } else {
            WS.send(JSON.stringify(switch_state));
            console.log(JSON.stringify(switch_state));
        }
    }
    console.log(switches);
    let switch_list = switches.map(d => <Switch key={d.id} valueId={d.id} value={d.value} handleSocketSend={socketSend} />);
    return (
        <> 
            <div className="col-xs-12  col-sm-6 col-xl-4 mt-4">
                <div className="m-0 p-2 SwitchBoard" style={{ backgroundColor: props.backColor }}>
                    <button className="btn btn-danger">off</button>
                    <h5 className="text-center">{props.labelname}</h5>
                    <div className="row">
                        {switch_list}
                    </div>
                    <span><small>Device Token:<b>{props.token}</b></small> {OnOff}</span>

                </div>

            </div>

        </>
    )

}

export default SwitchBoard
