import React, { useState, useRef } from 'react';
import { Link, NavLink, withRouter } from 'react-router-dom';
import { authUser,logout } from '../helper/authHelper';
import './Navbar.css';

function Navbar(props) {

    const inputRef = useRef(null);
    const [click, setClick] = useState(false);
    const hanldeClick = () => {
        setClick(!click);
        if (window.innerWidth <= 990) {
            inputRef.current.click();
        }
    };

    var component = [];
    var component2 = [];
    if (authUser()) {
        component.push(<li key="5" className="nav-item">
            <NavLink to='/dashboard' className="nav-link user-login-menue-nav" activeClassName="active" onClick={hanldeClick}>
                <button className="btn btn-outline-light btn-sm">Dashboard</button>
            </NavLink>
        </li>);

        component2.push(<NavLink key="6" to='/dashboard' className="nav-link" activeClassName="active" onClick={hanldeClick}>
            <button className="btn btn-outline-light btn-sm">Dashboard</button>
        </NavLink>);

        component.push(<li key="8" onClick={()=>{logout(()=>{props.history.push('/login')})}} className="nav-item">
            <a className="nav-link user-login-menue-nav" onClick={hanldeClick}>
                <button className="btn btn-outline-light btn-sm">Logout</button>
            </a>
        </li>);

        component2.push(<a key="7"  onClick={()=>{hanldeClick();logout(()=>{props.history.push('/login')})}} className="nav-link">
            <button className="btn btn-outline-light btn-sm">logout</button>
        </a>);

    }

    if (!authUser()) {
        component.push(<li key="1" className="user-login-menue-nav nav-item pull-right">
            <NavLink to='/login' className="nav-link" activeClassName="active" onClick={hanldeClick}>
                <button className="btn btn-outline-light btn-sm">Login</button>
            </NavLink>
        </li>
        );

        component.push(<li key="2" className="user-login-menue-nav nav-item pull-right">
            <NavLink to='/register' className="nav-link" activeClassName="active" onClick={hanldeClick}>
                <button className="btn btn-outline-light btn-sm">Register</button>
            </NavLink>
        </li>);

        component2.push(<NavLink key="3" to='/login' className="nav-link" activeClassName="active" onClick={hanldeClick}>
            <button className="btn btn-outline-light btn-sm">Login</button>
        </NavLink>);

        component2.push(<NavLink key="4" to='/register' className="nav-link" activeClassName="active" onClick={hanldeClick}>
            <button className="btn btn-outline-light btn-sm">Register</button>
        </NavLink>);

    }

    return (
        <>
            <section className="navigation">
                <nav className="navbar navbar-expand-lg navbar-dark fixed-top bg-dark">
                    <Link className="navbar-brand" to="/" onClick={hanldeClick}>SoftKliQ</Link>
                    <div className="navbar-toggler" data-toggle="collapse" ref={inputRef} data-target="#navbarCollapse"
                        aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation">

                        <i className={!click ? 'fa fa-bars' : 'fa fa-times'} onClick={hanldeClick} aria-hidden="true"></i>

                    </div>
                    <div className="collapse navbar-collapse bg-dark" id="navbarCollapse">
                        <ul className="navbar-nav mr-auto">
                            <li className="nav-item">
                                <NavLink to='/' className="nav-link" exact activeClassName="active" onClick={hanldeClick}>Home</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink to='/products' className="nav-link" activeClassName="active" onClick={hanldeClick}>Products</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink to='/contact-us' className="nav-link" activeClassName="active" onClick={hanldeClick}>Contact Us</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink to='/about-us' className="nav-link" activeClassName="active" onClick={hanldeClick}>About Us</NavLink>
                            </li>
                            {component}
                        </ul>
                    </div>
                    <div className="user-login-menue pull-right">
                        {component2}
                    </div>
                </nav>
            </section>
        </>
    )
}

export default withRouter(Navbar)
