import React from 'react';
import './App.css';
import { Switch, Route, Redirect} from 'react-router-dom'; //useHistory, Redirect
import Register from './components/pages/Register';
import Login from './components/pages/Login';
import Home from './components/pages/Home';
import ContactUs from './components/pages/ContactUs';
import Products from './components/pages/Products';
import Dashboard from './components/pages/Dashboard';
import AddNewDevice from './components/pages/AddNewDevice';
import NotFoundPage from './components/pages/NotFoundPage';
import { Offline, Online } from "react-detect-offline";
import { authUser } from './components/helper/authHelper';
import PrivateRoute from './components/helper/PrivateRoute';


const ConnectionState = () => (
  <div className="pt-2">
    <div className="pt-5">
      <Online><div style={{ backgroundColor: "#35dc4c", width: "100%", padding: "5px" }}>Only shown when you're online</div></Online>
      <Offline><div style={{ backgroundColor: "#dc3545", width: "100%", padding: "5px" }}>Only shown offline (surprise!)</div></Offline>
    </div>
  </div>
);

function App(props) {

  

  return (
    <>
      <ConnectionState />
      <Switch>
        <Route path='/' exact component={Home} />
        <Route path='/register' exact render={()=>(!authUser())?<Register/>:<Redirect to="/dashboard" />} />
        <Route path='/login' exact render={()=>(!authUser())?<Login/>:<Redirect to="/dashboard" />}  />
        <Route path='/contact-us' exact component={ContactUs} />
        <Route path='/products' exact component={Products} />
        <PrivateRoute path='/dashboard' exact component={Dashboard} />
        <PrivateRoute path='/add-new-device' exact component={AddNewDevice} />
        <Route component={NotFoundPage} />

      </Switch>
    </>
  );
}

