import React, { useState } from 'react';
import { Link, withRouter } from 'react-router-dom'
import './Register.css';
import Navbar from '../layouts/Navbar';
import Footer from '../layouts/Footer';
import axios from 'axios';
import { authenticate } from '../helper/authHelper';

function Login(props) {

    const [loginForm, setLoginform] = useState({
        email: '',
        password: ''
    });

    const [errorData, setErrorData] = useState([]);


    var { email, password } = loginForm;

    async function handleSubmit(e) {

        e.preventDefault();
        console.log(loginForm);
        e.preventDefault();
        try {


            const config = {
                headers: {
                    "Content-Type": "application/json"
                }
            }


            const res = await axios.post('/api/user/login', JSON.stringify(loginForm), config);
            console.log(res);
            
            setErrorData([]);
            authenticate(res, () => {
                props.history.push('/dashboard');
            });

        } catch (err) {
            var el = err.response.data.error;
            var errorMsg = [];
            if (err.response.data.error) {

                for (let i = 0; i < el.length; i++) {
                    errorMsg.push(<li key={"list" + i}> {el[i].msg} </li>);
                }
            }
            setErrorData(errorMsg);
        }

    }


    return (
        <>
            <Navbar />
            <div className="container pt-2">
                <div className="card bg-light">
                    <div className="card-body mx-auto" style={{ maxWidth: '400px' }}>
                        <h4 className="card-title mt-3 text-center">Login To Account</h4>
                        <p className="text-center">Get started with your free account</p>
                        <p>
                            <Link to="/" className="btn btn-block btn-twitter"> <i className="fab fa-twitter"></i>   Login via Twitter</Link>
                            <Link to="/" className="btn btn-block btn-facebook"> <i className="fab fa-facebook-f"></i>   Login via facebook</Link>
                        </p>
                        <p className="divider-text">
                            <span className="bg-light">OR</span>
                        </p>
                        <code>
                            <ul>{errorData}</ul>
                        </code>
                        <form id="login-form" onSubmit={handleSubmit} >
                            <div className="form-group input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text"> <i className="fa fa-envelope"></i> </span>
                                </div>
                                <input name="email" style={{ "width": "270px" }} className="form-control" placeholder="Email address" type="email"
                                    value={email} onChange={(e) => { setLoginform({ ...loginForm, email: e.target.value }) }} />
                            </div>
                            <div className="form-group input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text"> <i className="fa fa-lock"></i> </span>
                                </div>
                                <input name="password" className="form-control" placeholder="Create password" type="password"
                                    value={password} onChange={(e) => { setLoginform({ ...loginForm, password: e.target.value }) }} />
                            </div>
                            <div className="form-group">
                                <button type="submit" className="btn btn-primary btn-block"> Login  </button>
                            </div>
                            <p className="text-center">Create an account? <Link to="/register">Create Account</Link> </p>
                        </form>
                    </div>
                </div>
            </div>
            <Footer />
        </>
    )
}

export default withRouter(Login)
