import React from 'react';
import './ContactUs.css';
import { Link } from 'react-router-dom';
import Navbar from '../layouts/Navbar';
import Footer from '../layouts/Footer';

function ContactUs() {
    return (
        <>
            <Navbar />
                <div className="row pl-5 pt-5">
                    <div className="col-sm-4 col-xs-6">
                        <div className="card bg-light">
                            <div class="card-body">
                                <h4 className="card-title mt-3 text-center">CONTACT US</h4>
                                <p className="text-center">We would love to response back soon :)</p>

                                <form id="login-form" >
                                    <div className="form-group input-group">
                                        <input name="name" className="form-control" placeholder="Your Name" />
                                    </div>
                                    <div className="form-group input-group">
                                        <input name="email" className="form-control" placeholder="Your Email" />
                                    </div>
                                    <div className="form-group input-group">
                                        <input name="phone" className="form-control" placeholder="Your Phone Number" />
                                    </div>
                                    <div className="form-group input-group">
                                        <textarea name="message" className="form-control" placeholder="Type Your Message Here .." rows="5" ></textarea>
                                    </div>
                                    <div className="form-group">
                                        <button type="submit" className="btn btn-warning btn-block"> Get A Call Back  </button>
                                    </div>
                                    <p className="text-center"> <Link to="/register">Back To Previous Page</Link> </p>
                                </form>

                            </div>
                        </div>
                    </div>
                    <div className="col-sm-8 col-xs-6">
                    </div>
                </div>
            <Footer />
        </>
    )
}

export default ContactUs
